.close:hover {
  opacity: 1;
}

.close:before,
.close:after {
  z-index: 20;
  position: absolute;
  left: 15px;
  content: ' ';
  height: 25px;
  width: 7px;
  background-color: #2196F3;
}

.close:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.close:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

