.login-screen {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to bottom right, #1976d2, #9de3dc, #0d11b4);
  overflow: hidden;
}
.login-screen-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 15%;
  z-index: 2;
}
.login-screen-button {
  position: relative;
  z-index: 2;
  width: 50%;
  background: #fff;
  border: none;
  outline: none;
  height: 50px;
  line-height: 50px;
  color: #000;
  font-weight: 700;
  pointer-events: none;
}
.login-screen-button:hover {
  cursor: pointer;
  text-decoration: none;
}
.login-screen-button-enabled {
  pointer-events: all;
  text-decoration: underline;
  background: #f5f5f5;
}
.login-screen-greetings {
  color: #fff;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
}
.login-button-container {
  width: 500px;
  margin: 40px auto 0;
}
.form-group {
  margin-top: 20px;
  position: relative;
}
.form-container {
  position: relative;
  min-height: 250px;
  width: 500px;
  background: #fff;
  margin: 0 auto;
  padding: 25px 50px 85px;
}
.form-control {
  width: 100%;
  border: none;
  border-radius: 10px;
  background: #f5f5f5;
  height: 25px;
  line-height: 25px;
  padding-left: 10px;
  margin-top: 5px;
}
.registerForm .register-buttons {
  padding-top: 40px;
  justify-content: space-between;
  display: flex;
  align-items: center;
}
.registerForm .register-buttons a {
  font-size: 16px;
  text-decoration: underline;
}
.registerForm .register-buttons a:hover {
  cursor: pointer;
  text-decoration: none;
}
.registerForm .btn {
  position: unset;
  bottom: 0;
  margin: unset;
}
.password-eye {
  position: absolute;
  background: url('../img/icons/eye.png') no-repeat center / cover;
  width: 20px;
  height: 20px;
  z-index: 10;
  right: 10px;
  top: 29px;
  opacity: .5;
  cursor: pointer;
}
@media all and (max-width: 600px) {
  .form-container {
    width: 100%;
    padding: 25px 15px 85px;
    height: 50vh;
    overflow: auto;
    position: relative;
  }
  .login-button-container {
    width: 100%;
  }
  .login-button-container button > span {
    font-size: 12px;
  }
  .login-screen-container {
    top: 5%;
    height: 50vh;
  }
  .login-screen-button-enabled {
    text-decoration: unset !important;
  }
}
